  const pattern = new Pattern("один ряд 1000х3000(простенок h2700, карниз h300) + 2000x3000(простенком снизу h600, окно h2100, карниз h300) + 1000х3000(простенок h2700, карниз h300)", facadeRows, { email: "", name: "" }, "testPatternId_2", []);

